import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireObject } from '@angular/fire/database';
import { ActivatedRoute } from '@angular/router';
import { ContentService } from '../core/content.service';
import { Production } from '../core/production';

@Component({
  selector: 'app-edit-production',
  templateUrl: './edit-production.component.html',
  styleUrls: ['./edit-production.component.css']
})
export class EditProductionComponent implements OnInit {

  private editedProduction: Production = new Production();
  private productionID: string;
  private productionReference: AngularFireObject<any>;

  ngOnInit() { }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private afDb: AngularFireDatabase,
    private contentService: ContentService
  ) {
    this.getProduction();
  }

  getProduction(): void {
    this.productionID = this.route.snapshot.paramMap.get('key');
    this.productionReference = this.afDb.object('production/' + this.productionID);
    this.productionReference.snapshotChanges().subscribe(action => {
      this.editedProduction = action.payload.val() as Production;
    });
  }

  onSubmit(form: NgForm) {
    if (form.value.key == null) {
      this.contentService.updateProduction(form.value);
    }
    this.router.navigate(['/production']);
  }

  onCancel() {
    this.router.navigate(['/production']);
  }
}
