import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';

declare var google: any;

@Component({
  selector: 'app-new-farm',
  templateUrl: './new-farm.component.html',
  styleUrls: ['./new-farm.component.css']
})
export class NewFarmComponent implements OnInit {
  public regionsList = ['Heraklion Crete', 'Chania Crete', 'Rehtimnon Crete', 'Lasithi Crete'];
  public soilTypesList = ['Loam', 'Clay', 'Sand', 'Silt', 'Peat', 'Chalk'];
  public farmerID: string;
  public latitudeT;
  public longitudeT;
  private defaultCoordinates = new google.maps.LatLng(0, 0);

  ngOnInit() {
    this.initMap(this.defaultCoordinates);
  }

  constructor(
    private router: Router,
    private contentService: ContentService
  ) {
    this.latitudeT = 35.3900078;
    this.longitudeT = 25.0834658;
    this.defaultCoordinates = new google.maps.LatLng(this.latitudeT, this.longitudeT);
    this.farmerID = firebase.auth().currentUser.uid;
    this.contentService.getFarmStatistics();
  }

  onSubmit(form: NgForm) {
    if (form.value.key == null) {
      this.contentService.insertFarm(form.value);
    }
    this.router.navigate(['/farms']);
  }

  initMap(coordinates) {
    const map = new google.maps.Map(document.getElementById('map'), {
      zoom: 8,
      center: coordinates,
      scrollwheel: false
    });
    const marker = new google.maps.Marker({ position: coordinates });
    marker.setMap(map);

    map.addListener('click', (event) => {
      marker.setPosition(new google.maps.LatLng(event.latLng.lat(), event.latLng.lng()));
      map.panTo(new google.maps.LatLng(event.latLng.lat(), event.latLng.lng()));
      this.latitudeT = event.latLng.lat();
      this.longitudeT = event.latLng.lng();
    });
  }
}
