import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';
import { Income } from '../core/income';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-incomes',
  templateUrl: './incomes.component.html',
  styleUrls: ['./incomes.component.css']
})
export class IncomesComponent implements OnInit {

  public tableData: TableData;
  farmerID: string;

  incomesList: Income[];
  constructor(
    private router: Router,
    private contentService: ContentService
  ) {
    this.farmerID = firebase.auth().currentUser.uid;
  }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Title', 'Ammount', 'Date', 'Description']
    };
    const x = this.contentService.getIncomesData();
    x.snapshotChanges().subscribe(item => {
      this.incomesList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['iid'] = element.key;
        if (this.farmerID === (y as Income).farmerid) {
          this.incomesList.push(y as Income);
        }
      });
    });
  }

  editIncome(income: Income) {
    this.router.navigate(['/edit-income/' + income.iid]);
  }

  deleteIncome(income: Income) {
    this.contentService.deleteIncome(income.iid);
  }

  addNewIncome() {
    this.router.navigate(['/new-income']);
  }
}
