import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';
import { Outcome } from '../core/outcome';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-outcomes',
  templateUrl: './outcomes.component.html',
  styleUrls: ['./outcomes.component.css']
})
export class OutcomesComponent implements OnInit {

  public tableData: TableData;
  farmerID: string;
  outcomesList: Outcome[];
  constructor(
    private router: Router,
    private contentService: ContentService
  ) {
    this.farmerID = firebase.auth().currentUser.uid;
  }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Title', 'Ammount', 'Date', 'Description']
    };
    const x = this.contentService.getOutcomesData();
    x.snapshotChanges().subscribe(item => {
      this.outcomesList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['oid'] = element.key;
        if (this.farmerID === (y as Outcome).farmerid) {
          this.outcomesList.push(y as Outcome);
        }
      });
    });
  }

  editOutcome(outcome: Outcome) {
    this.router.navigate(['/edit-outcome/' + outcome.oid]);
  }

  deleteOutcome(outcome: Outcome) {
    this.contentService.deleteOutcome(outcome.oid);
  }

  addNewOutcome() {
    this.router.navigate(['/new-outcome']);
  }

}
