import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';
import { Farm } from '../core/farm';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-farms',
  templateUrl: './farms.component.html',
  styleUrls: ['./farms.component.css']
})
export class FarmsComponent implements OnInit {

  public tableData: TableData;
  public farmerID: string;
  public farmsList: Farm[];

  constructor(
    private router: Router,
    private contentService: ContentService
  ) {
    this.farmerID = firebase.auth().currentUser.uid;
  }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Title', 'Region', 'Size', 'Soil', 'Description']
    };
    const x = this.contentService.getFarmsData();
    x.snapshotChanges().subscribe(item => {
      this.farmsList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['fid'] = element.key;
        if (this.farmerID === (y as Farm).farmerid) {
          this.farmsList.push(y as Farm);
        }
      });
    });
  }

  editFarm(farm: Farm) {
    this.router.navigate(['/edit-farm/' + farm.fid]);
  }

  deleteFarm(farm: Farm) {
    this.contentService.deleteFarm(farm.fid);
  }

  addNewFarm() {
    this.router.navigate(['/new-farm']);
  }
}
