import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireObject } from '@angular/fire/database';
import { ActivatedRoute } from '@angular/router';
import { ContentService } from '../core/content.service';
import { Income } from '../core/income';

@Component({
  selector: 'app-edit-income',
  templateUrl: './edit-income.component.html',
  styleUrls: ['./edit-income.component.css']
})
export class EditIncomeComponent implements OnInit {

  private editedIncome: Income = new Income();
  private incomeID: string;
  private incomeReference: AngularFireObject<any>;

  ngOnInit() { }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private afDb: AngularFireDatabase,
    private contentService: ContentService
  ) {
    this.getIncome();
  }

  getIncome(): void {
    this.incomeID = this.route.snapshot.paramMap.get('key');
    this.incomeReference = this.afDb.object('incomes/' + this.incomeID);
    this.incomeReference.snapshotChanges().subscribe(action => {
      this.editedIncome = action.payload.val() as Income;
    });
  }

  onSubmit(form: NgForm) {
    if (form.value.key == null) {
      this.contentService.updateIncome(form.value);
    }
    this.router.navigate(['/incomes']);
  }

  onCancel() {
    this.router.navigate(['/incomes']);
  }
}
