import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';

@Component({
  selector: 'app-new-income',
  templateUrl: './new-income.component.html',
  styleUrls: ['./new-income.component.css']
})
export class NewIncomeComponent implements OnInit {

  farmerID: string;

  ngOnInit() { }

  constructor(
    private router: Router,
    private contentService: ContentService
  ) {
    this.farmerID = firebase.auth().currentUser.uid;
    this.contentService.getIncomeStatistics();
  }

  onSubmit(form: NgForm) {
    if (form.value.key == null) {
      this.contentService.insertIncome(form.value);
    }
    this.router.navigate(['/incomes']);
  }
}
