import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireObject } from '@angular/fire/database';
import { ActivatedRoute } from '@angular/router';
import { ContentService } from '../core/content.service';
import { Farm } from '../core/farm';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

declare const google: any;

@Component({
  selector: 'app-edit-farm',
  templateUrl: './edit-farm.component.html',
  styleUrls: ['./edit-farm.component.css']
})
export class EditFarmComponent implements OnInit {

  public regionsList = ['Heraklion Crete', 'Chania Crete', 'Rehtimnon Crete', 'Lasithi Crete'];
  public soilTypesList = ['Loam', 'Clay', 'Sand', 'Silt', 'Peat', 'Chalk'];
  public editedFarm: Farm = new Farm();
  public farmID: string;
  private farmReference: AngularFireObject<any>;

  ngOnInit() {}

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private afDb: AngularFireDatabase,
    private contentService: ContentService,
    private http: HttpClient
  ) {
    this.getFarm();
  }

  public getJSON(): Observable<any> {
    return this.http.get('./assets/mapstyle.json');
  }

  getFarm(): void {
    this.farmID = this.route.snapshot.paramMap.get('key');
    this.farmReference = this.afDb.object('farms/' + this.farmID);
    this.farmReference.snapshotChanges().subscribe(action => {
      this.editedFarm = action.payload.val() as Farm;
      this.initMap(new google.maps.LatLng(this.editedFarm.latitude, this.editedFarm.longitude));
    });
  }

  onSubmit(form: NgForm) {
    if (form.value.key == null) {
      this.contentService.updateFarm(form.value);
    }
    this.router.navigate(['/farms']);
  }

  onCancel() {
    this.router.navigate(['/farms']);
  }

  initMap(coordinates) {
    const map = new google.maps.Map(document.getElementById('map'), {
      zoom: 8,
      center: coordinates,
      scrollwheel: false
    });
    const marker = new google.maps.Marker({ position: coordinates });
    marker.setMap(map);

    map.addListener('click', (event) => {
      marker.setPosition(new google.maps.LatLng(event.latLng.lat(), event.latLng.lng()));
      map.panTo(new google.maps.LatLng(event.latLng.lat(), event.latLng.lng()));
      this.editedFarm.latitude = event.latLng.lat();
      this.editedFarm.longitude = event.latLng.lng();
    });
  }
}
