import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';
import { Production } from '../core/production';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-production',
  templateUrl: './production.component.html',
  styleUrls: ['./production.component.css']
})
export class ProductionComponent implements OnInit {

  public tableData: TableData;
  farmerID: string;
  date: string;

  productionList: Production[];
  constructor(
    private router: Router,
    private contentService: ContentService
  ) {
    this.farmerID = firebase.auth().currentUser.uid;
  }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Title', 'Ammount Weight', 'Ammount', 'Date', 'Description']
    };
    const x = this.contentService.getProductionData();
    x.snapshotChanges().subscribe(item => {
      this.productionList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['pid'] = element.key;
        if (this.farmerID === (y as Production).farmerid) {
          this.productionList.push(y as Production);
          const tempDate = new Date((y as Production).registrationDate);
          this.date = String(tempDate.getMonth() + 1) + '/' + String(tempDate.getFullYear());
         }
      });
    });
  }

  editProduction(production: Production) {
    this.router.navigate(['/edit-production/' + production.pid]);
  }

  deleteProduction(production: Production) {
    this.contentService.deleteProduction(production.pid);
  }

  addNewProduction() {
    this.router.navigate(['/new-production']);
  }
}
