import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireObject } from '@angular/fire/database';
import { ActivatedRoute } from '@angular/router';
import { ContentService } from '../core/content.service';
import { Outcome } from '../core/outcome';

@Component({
  selector: 'app-edit-outcome',
  templateUrl: './edit-outcome.component.html',
  styleUrls: ['./edit-outcome.component.css']
})
export class EditOutcomeComponent implements OnInit {

  private editedOutcome: Outcome = new Outcome();
  private outcomeID: string;
  private outcomeReference: AngularFireObject<any>;

  ngOnInit() { }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private afDb: AngularFireDatabase,
    private contentService: ContentService
  ) {
    this.getOutcome();
  }

  getOutcome(): void {
    this.outcomeID = this.route.snapshot.paramMap.get('key');
    this.outcomeReference = this.afDb.object('outcomes/' + this.outcomeID);
    this.outcomeReference.snapshotChanges().subscribe(action => {
      this.editedOutcome = action.payload.val() as Outcome;
    });
  }

  onSubmit(form: NgForm) {
    if (form.value.key == null) {
      this.contentService.updateOutcome(form.value);
    }
    this.router.navigate(['/outcomes']);
  }

  onCancel() {
    this.router.navigate(['/outcomes']);
  }
}
